.sidebar-layout {

  @media (max-width: @xl) {
    &__catalog {
      display: none;
    }

    &__card {
      max-width: 100%;
    }

  }

  @media (max-width: @md) {
    &__content {
      display: block;
    }

    &__catalog {
      margin-right: 0;
      margin-bottom: 2rem;
      max-width: 100%;
    }

    &__card {
     max-width: 100%;
    }

  }
}