.receive-newsletter {
  @media (max-width: @md) {
    &__row {
      display: block;
    }

    &__col {
      width: 100%;
      margin-bottom: 1rem;

      &:first-child {
        width: 100%;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

  }

  @media (max-width: @md) {
    &__col {
      display: block;

      & .input {
        margin-right: 0;
        margin-bottom: 1rem;
      }
    }

    &__btn {
      width: 100%;
    }

  }

}