.characteristics {

  &__inner {
    padding: 5rem;
    border-radius: 3px;
    background-color: @c-light-purple;
  }

  &__items {
    display: flex;
    margin-bottom: 3rem;
  }

  &__col {
    width: 50%;
  }

  &__list {
    .resetList();
  }

  &__article {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
    padding-left: 1.1rem;

    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);

      width: 0.6rem;
      height: 0.6rem;
      border-radius: 50%;
      background-color: @c-black;
    }

    span {
      &:first-child {
        font-weight: 600;
        margin-right: 0.5rem;
      }
    }
  }

}