.burger {
  .resetBtn();
  width: 2.5rem;
  height: 2rem;
  position: relative;


  &::before, &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 0.2rem;
    background-color: @c-black-light;
    transition: 1s;
  }

  &::before {
    top: 0;
  }

  &::after {
    bottom: 0;
  }

  span {
    background-color: @c-black-light;
    width: 100%;
    height: 0.2rem;
    position: absolute;
    transform: translateY(-50%);

  }


}