.ware {
margin-bottom: 4rem;

  &__inner {
    display: flex;
    justify-content: space-between;

  }

  &__left {
    width: 100%;
    max-width: 44.5rem;
    margin-right: 3rem;
  }

  &__right {
    width: 100%;
    max-width: 63.5rem;
  }

  &__img {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    max-height: 44.5rem;
    overflow: hidden;

    margin-bottom: 1.5rem;

    &::before {
      content: '';
      display: block;
      width: 100%;
      padding-top: 100%;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__slider {
    overflow: hidden;
  }

  &__slider-list {
    display: flex;
  }

  &__slider-item {
    width: calc((100% - 2 * 0.8rem) / 3);
    flex-shrink: 0;
    overflow: hidden;
    border: 1px solid transparent;
    margin-right: 0.8rem;

    cursor: pointer;
    border-radius: 0.3rem;
    transition: @time;

    &._active {
      border: 1px solid @c-gray;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__slider-img {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    max-height: 14rem;
    overflow: hidden;

    &::before {
      content: '';
      display: block;
      width: 100%;
      padding-top: 100%;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__availability {
    svg {
      width: 1.5rem;
      height: 1.5rem;
      fill: #18EA7C;
    }
  }

  &__head {
    display: flex;
    justify-content: space-between;
    margin-bottom: 4rem;
  }

  &__head-col {
    margin-right: 1rem;

    &:first-child {
      flex-basis: 44.5rem;
    }

    &:last-child {
      flex-shrink: 0;
      margin-right: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-end;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 2.4rem;
    line-height: 1.2;
    color: #000000;

    margin-bottom: 0.5rem;
  }

  &__code {

  }

  &__status {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    font-size: 1.8rem;
    line-height: 1.2;
    color: @c-black;

    margin-bottom: 1.5rem;
  }

  &__code {
    font-size: inherit;
  }

  &__tick {
    font-size: inherit;
  }

  &__delivery {
    font-weight: 600;
    font-size: 2rem;
    line-height: 1.2;
  }

  &__price {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  &__share {
    display: flex;
    align-items: center;
    color: initial;

    svg {
      width: 2rem;
      height: 2rem;
      fill: @c-black;
      margin-right: 0.5rem;
    }

  }

  &__availability {
    display: flex;
    align-items: center;
  }

  &__tick {
    width: 1.5rem;
    height: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: @c-light-green;


    margin-right: 0.5rem;

    svg {
      width: 0.9rem;
      height: 0.8rem;
      fill: @c-white;
    }
  }

  &__new-price {
    font-weight: 600;
    font-size: 3rem;
    line-height: 1.2;
    margin-bottom: 0.5rem;
  }

  &__old-price {
    font-weight: 500;
    font-size: 2rem;
    line-height: 1.2;
    text-decoration: line-through;

    color: @c-gray;
  }

  &__quantity {
    margin-bottom: 4rem;
  }

  &__buttons {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 4rem;
  }

  &__btn {
    transition: @time;

    &:first-child {
      margin-right: 3rem;
    }

    &:last-child {
      color: @c-black;
      border: 1px solid @c-black;
      background-color: transparent;

      &:hover {
        color: @c-white;
        border: 1px solid @c-black;
        background-color: @c-black;
      }
    }
  }

  &__advertising-title {
    font-weight: 600;
    font-size: 2.4rem;
    line-height: 1.2;

    margin-bottom: 1.6rem;
  }

  &__advertising {
    padding: 2rem 1rem;
    background-color: @c-light-purple;
    display: flex;
    flex-direction: column;
    align-items: center;

    box-shadow: @box-shadow;
  }

  &__advertising-content {
   display: flex;
    align-items: center;

    margin-bottom: 1.5rem;

    svg {
      width: 2rem;
      height: 2rem;
      margin-right: 2rem;
    }
  }

}