.popular {
  @media (max-width: @md) {
    &__item {
      width: 17.5rem;
    }

    &__wrap-slider {
      margin: 0 0 1.5rem ;
    }

    &__carousel-navs {
      justify-content: center;

      & .pagination {
        display: flex;
      }
    }

    &__carousel-nav {
      display: none;
    }

  }
}