.header {
  @media (max-width: @lg){

    padding: 0.8rem 0;
    margin-bottom: 0;
    border-bottom: 1px solid @c-black-light;

    .header__content-burger {
      order: 1;
      display: block;

      margin-right: 2rem;
    }

    &__search {
      order: 2;
      width: auto;
      flex-basis: auto;

    }

    &__logo {
      flex-grow: 1;
      order: 3;
    }

    &__logo-inner {
      margin: 0 auto;
    }

    &__basket {
      order: 4;
      &-name {
        display: none;
      }
    }

    &__top {
      display: none;
    }

    &__phones {
      display: none;
    }

    &__bottom {
     display: none;
    }

    &__content-inner {
      margin: auto;

      & > * {
        margin-left: 0;
        margin-bottom: 0;
      }
    }

    &__search-inner {
      border: none;
    }

    &__search-input {
      display: none;
    }

  }
}