.exit {
  margin: 4rem 0 6rem;

  &__title {
    font-weight: 500;
    font-size: 4rem;
    letter-spacing: 0.1em;
    text-align: center;

    margin-bottom: 4rem;
  }

  &__row {
    display: flex;
    margin: 0 -1.5rem;
  }

  &__col {
    margin: 0 1.5rem;
    flex-basis: 54rem;

    & .input__title {
      font-weight: 600;
      font-size: 1.8rem;

    }

    & .characteristics__list {
      margin-bottom: 2rem;
    }

    & .characteristics__article {
      margin: 0;

      &::before {
        top: 0.7rem;
        transform: translateY(0);
      }
    }
  }

  &__col-item {
    margin-bottom: 2rem;

    &:last-child {
      margin-bottom: 1.5rem;
    }
  }

  &__forgot-password {
    color: inherit;
    display: block;
    margin-bottom: 2rem;
  }

  &__btn {
    .resetBtn();
    background-color: @c-black-light;
    color: white;
    width: 100%;
    font-weight: 600;

    &:hover {
      border: 1px solid @c-black-light;
      background-color: @c-light-green;
      color: @c-black-light;
    }
  }

  &__col-title {
    font-weight: 600;
    font-size: 2.4rem;
    margin-bottom: 1.5rem;
    text-align: center;
  }

  &__col-text {
    font-weight: 600;
    font-size: 1.8rem;
    margin-bottom: 0.5rem;
  }

  &__col-item-group {
    display: flex;
    flex-direction: row;

    & .input__input {
      flex-basis: 12rem;
      margin-right: 1rem;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__select {
    padding: 1.1rem 2rem;
  }

}