.btn {
  .resetBtn();
  align-items: center;
  justify-content: center;
  font-weight: 600;
  background-color: @c-black-light;
  color: white;
  min-height: 4rem;
  padding: 1.1rem 2.5rem;
  border-radius: 0.3rem;
  border: 1px solid transparent;

  transition: @time;

  &:hover {
    border: 1px solid @c-black-light;
    background-color: @c-gray;
  }
}