.quantity {

  &__inner {
    display: flex;
  }

  &__number {
    .resetBtn();
    width: 4.4rem;
    height: 4.4rem;
    border: 1px solid @c-black;
    border-radius: 0.3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.5;

    transition: @time;

    &:hover {
      opacity: 1;
    }

    svg {
      width: 1.5rem;
      height: 3rem;
    }

    &._plus {
      transform: rotate(-90deg);
    }

    &._minus {
      transform: rotate(90deg);
    }
  }

  &__input {
    border: none;
    text-align: center;
    max-width: 5rem;
    width: 100%;

    font-size: 3rem;
    line-height: 1.2;
    padding: 0 0.5rem;
  }


}