.main-carousel {
  position: relative;
  @media (max-width: @md) {

    &__item-content {
      position: relative;
    }

    &__item-content {
      padding: 1rem 0 4rem 1rem;
    }

    &__item-title {
      text-align: start;

      font-size: 2rem;
      line-height: 1.2;
    }

    &__item-btn {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
    }
  }

  @media (max-width: @sm) {
    &__item-right {

    }
  }

}