.select-dropdown {
  display: inline-flex;
  position: relative;
  font-size: 1.5rem;
  justify-content: center;

  &__trigger {
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;

    svg {
      width: 1.1rem;
      height: 1.1rem;
      margin-left: .4rem;
      transition: transform @time;
    }

    span {
      .clipText();
      max-width: 6rem;
    }
  }

  &__dropdown {
    position: absolute;
    top: calc(100% + 2rem);
    display: none;
    transition-property: opacity, visibility, transform;
    transition-duration: @time;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform-origin: center top;
    left: 50%;
    transform: translateX(-50%) translateY(-1rem);
    //display: none;
    justify-content: center;
    z-index: 30;

    &-corner {
      position: absolute;
      display: block;
      border: 1.3rem solid transparent;
      border-top: 0;
      border-bottom-color: #F0F0F0;
      border-bottom-width: 1.8rem;
      left: 50%;
      transform: translateX(-50%);
      bottom: calc(100% - .2rem);
      width: 2.6rem;
      height: 1.8rem;
    }

    &-inner {
      background-color: #F0F0F0;
      padding: 1.5rem 1.6rem 1.2rem;
      border-radius: 1rem;
      box-shadow: 0 1rem 1rem fade(#000000, 50);
      margin-bottom: 2rem;
    }

    &-list {
      margin: 0;
      padding: 0;
      list-style: none;
      //white-space: nowrap;
      display: flex;
      flex-direction: column;
      max-height: 22rem;
      flex-wrap: wrap;
      margin-right: -2rem;
      max-width: calc(100vw - 5rem);
      overflow-y: auto;


      li {
        padding-bottom: 1rem;
        max-width: 20rem;
        padding-right: 2rem;
        flex-shrink: 0;
      }

      a {
        &._active {
          color: black;
          pointer-events: none;
          text-decoration: none;
        }
      }
    }
  }

  &._active {
    .select-dropdown {
      &__trigger {
        svg {
          transform: rotate(-180deg);
        }
      }

      &__dropdown {
        opacity: 1;
        visibility: visible;
        pointer-events: inherit;
        transform: translateX(-50%);
      }
    }
  }

  &._lang {
    .select-dropdown__trigger {
      span {
        color: @c-white;
      }
      svg {
        fill: @c-white;
      }
    }
  }

  &._phones {
    .select-dropdown__trigger {
      span {
        max-width: 16rem;
        width: 100%;
      }
      svg {
        fill: @c-black-light;
      }
    }
    .select-dropdown__dropdown-inner {
      width: 18rem;

    }
  }

  &__phones-list {
    .resetList();
    li {
      margin-bottom: 1.5rem;
      a {
        color: inherit;
        text-decoration: none;
        display: flex;
        justify-content: center;
      }
    }
  }


}