.pagination-numeric {
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__navs {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 4rem;
    height: 4rem;
    border: 1px solid @c-black-light;
    border-radius: 0.4rem;
    cursor: pointer;

    transition: @time;

    &:hover {
      background-color: @c-black-light;
      svg {
        fill: @c-white;
      }
    }

    &-left {
        transform: rotate(90deg);
    }
    &-right {
        transform: rotate(-90deg);
    }

    svg {
      width: 0.9rem;
      height: 1.8rem;
      fill: @c-black;
    }
  }

  &__pagination-number {
    display: flex;
    justify-content: center;
    margin: 0 1rem;

    span {
      margin-right: 1rem;
      align-self: flex-end;
    }
  }

  &__pagination {
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 4rem;
    height: 4rem;
    border: 1px solid @c-black-light;
    border-radius: 0.4rem;
    color: @c-black;
    margin-right: 1rem;
    cursor: pointer;

    &:last-child {
      margin-right: 0;
    }

    &._active {
      border: 1px solid @c-black-light;
      background-color: @c-black-light;
      color: #FFFFFF;
    }

  }

}