.input {

  display: flex;
  flex-direction: column;
  position: relative;
  color: @c-black;
  align-self: flex-start;
  pointer-events: none;
  font-size: 1.4rem;
  letter-spacing: 0.05em;

  &__title {
    align-self: flex-start;
    display: inline-block;
    margin-bottom: 0.5rem;
    color: @c-black;
    font-size: inherit;
    font-weight: 300;
    pointer-events: auto;
  }

  &__required {
    color: @c-red;
  }

  &__input {
    width: 100%;
    padding: 1.4rem 2rem;
    border-radius: 3px;
    background-color: white;
    transition: border-color @time, outline-color @time;
    pointer-events: auto;
    border: 1px solid @c-gray-light;

    &:focus {
      border: 1px solid @c-black-light;
    }
  }

  &._error &__input {
    border-color: @c-red;
    outline-color: @c-red;
  }

  &__error {
    max-height: 0;
    margin: 0;
    padding: 0;
    overflow: hidden;

    font-size: 1.2rem;
    color: @c-red;

    transition: @time-modal;
  }

  &._error &__error {
    max-height: 8rem;
    margin-top: 0.2rem;
  }

  &._error &__title {
    color: @c-red;
  }

}