.resetList() {
  list-style: none;
  margin: 0;
  padding: 0;
}

.resetBtn() {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: left;
  font: inherit;
  border: 0;
  outline: 0;
  padding: 0;
  text-decoration: none;
  background-color: transparent;
  color: inherit;
  word-break: break-word;
  cursor: pointer;
}

.clipText() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}