*,
::after,
::before {
  box-sizing: inherit;
  outline: none;
}


html {
  box-sizing: border-box;
  font-size: @f-root-size;
  min-width: @min-width;
}

body {
  -webkit-tap-highlight-color: transparent;
  text-rendering: optimizeLegibility;

  font-family: @f-base-family;
  font-size: @f-base-size;
  font-weight: @f-base-weight;
  line-height: @f-base-height;
  word-wrap: break-word;
  color: @c-black;

  overflow-x: hidden;
  overflow-y: auto;

  &._scroll-lock {
    overflow: hidden;
  }

}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  fill: currentColor;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


input,
textarea {


  font: inherit;
  border: 0;
  background: white;
  padding: 1rem 1rem .9rem;
  line-height: 1.125;
  color: @c-black;
}

textarea {
  resize: vertical;
}

select {
  width: 100%;
  padding: 1.1rem 0;
  margin: 0 1px;
  border-radius: 0.3rem;
  border: 1px solid @c-black-light;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5 {
  font-weight: bold;
  //margin: 0 0 3rem;
}

h1, .h1 {
  font-size: 4rem;
  line-height: 1.2;
  text-transform: uppercase;
}

h2, .h2 {
  font-size: 3rem;
  line-height: 1.2;
  text-transform: uppercase;
}

h3, .h3 {
  font-size: 2rem;
  line-height: 1.2;
  text-transform: uppercase;
}

h4, .h4 {
  font-size: 1.8rem;
  line-height: 1.22;
  text-transform: uppercase;
}