.product-tabs {
  margin-bottom: 6rem;

  &__items {
    display: flex;
    //justify-content: center;

    margin-bottom: 2rem;
  }

  &__item {
    flex-shrink: 0;

    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.2;
    color: @c-black;
    text-decoration: none;

    margin: 0 2rem;

    &._active {
      color: @c-gray;
      text-decoration: underline;
    }
  }

  &__content-item {
    display: none;

    &._active {
      display: block;
    }
  }

}
