.add-review {


  &__inner {
    background: #F5F5F5;
    border-radius: 3px;

    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 3rem;
  }

  &__textarea {
    min-height: 15rem;
  }

  &__col {
    width: 40%;
    flex-basis: 45rem;
    flex-grow: 1;

    &:first-child {
      margin-right: 5rem;
    }
  }

  &__label {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__rating-item {
    display: flex;
    align-items: center;
  }

  &__rating-point {
    margin-left: 1rem;

    font-size: 1.8rem;
    color: @c-black;
  }

  &__btn {
    margin: 0 auto;
  }

}