.reviews {

  &__inner {
    padding: 3rem;
    border-radius: 3px;
    background-color: @c-light-purple;
  }

  &__inner-head {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;

    margin-bottom: 2rem;
  }

  &__title {
    font-weight: 500;
    font-size: 2rem;
    margin-left: 7.5rem;
  }

  &__btn {
    background-color: transparent;
    color: @c-black;
    border-radius: 0.3rem;
    border: 1px solid @c-black;

    font-weight: normal;
    font-size: 1.8rem;
    letter-spacing: 0.05em;

    transition: @time;

    &:hover {
      background-color: @c-white;
    }
  }

  &__inner-bottom {
    position: relative;
  }

  &__slider {
    position: relative;
    margin: 0 7.5rem;
    overflow: hidden;

  }

  &__slider-wrap {
    display: flex;
  }

  &__slider-slide {
    flex-shrink: 0;
    width: 100%;

    margin-right: 4rem;

    &:last-child {
      margin-right: 0;
    }
  }

  &__outer {
    margin-bottom: 2rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__user-name {
    font-weight: 600;
    font-size: 1.8rem;
    color: @c-gray;
  }

  &__data {
    font-size: 1.6rem;
    margin-bottom: 1.5rem;
  }

  &__evaluation {
    margin-bottom: 1rem;
  }

  &__rating {
    display: flex;
    align-items: center;
  }

  &__rating-title {
    margin-left: 1rem;
  }

  &__carousel-navs {
    display: flex;
    justify-content: space-between;

    & .pagination {
      display: none;
    }

    & .carousel-nav {
      position: absolute;
      top: 50%;


     &._prev {
       left: 0;
       transform: translateY(-50%) rotate(90deg);
     }

      &._next {
        right: 0;
        transform: translateY(-50%) rotate(-90deg);
      }

    }
  }

}