.card {

  &__title {
    font-size: 4rem;
    line-height: 1.2;
    color: @c-black;
    margin-bottom: 1rem;

    span {
      color: @c-light-green;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -1.5rem;
    margin-right: -1.5rem;
  }

  &__item {
    padding: 1.5rem;
    width: calc(100% / 4);
    //margin: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    transition: @time;

    &:hover {
      box-shadow: @box-shadow;
      .card__img {
        img {
          transform: scale(1.2);
        }
      }
    }
  }

  &__link {
    display: flex;
    flex-direction: column;
    color: @c-black;
    text-decoration: none;
    margin-bottom: 2rem;
  }

  &__img {
    overflow: hidden;
    display: flex;
    position: relative;
    width: 100%;
    margin-bottom: 2rem;

    &::before {
      content: '';
      display: block;
      width: 100%;
      padding-top: 100%;
    }

    img {
      position: absolute;
      left: 0;
      top: 0;
      object-fit: cover;
      width: 100%;
      height: 100%;
      transition: @time;
    }
  }

  &__item-subtitle {
    font-weight: 500;
    font-size: 1.4rem;
    line-height: 1.2;
    text-align: center;
    color: #5D5959;
    margin-bottom: 0.5rem;
  }

  &__item-title {
    text-align: center;
    margin-bottom: 1.5rem;
  }

  &__item-former {
    font-weight: 500;
    font-size: 1.4rem;
    line-height: 1.2;

    text-decoration: line-through;
    text-align: center;
    color: @c-gray-light;
    margin-bottom: 0.5rem;
  }

  &__item-price {
    font-weight: 600;
    font-size: 2rem;
    line-height: 1.2;

    margin-top: auto;

    text-align: center;
    color: @c-black;

  }

  &__item-button {
    border-radius: 0.3rem;
  }

}