.checkbox {

  display: inline-flex;
  user-select: none;
  align-items: center;
  cursor: pointer;

  font-weight: 300;
  color: @c-black-light;

  input {
    display: none;
  }

  input:checked + &__mark {
    background-color: black;
    &::before {
      transform: scale(1) rotate(-45deg);
    }
  }

  &__mark {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 2rem;
    height: 2rem;
    background-color: transparent;
    border: 1px solid @c-black-light;
    flex-shrink: 0;
    border-radius: 0.3rem;
    transition: background-color @time;
    margin-right: 1.5rem;

    &::before {
      content: '';
      display: inline-flex;
      width: 1.2rem;
      height: .7rem;
      border-bottom: .2rem solid #fff;
      border-left: .2rem solid #fff;
      transform: scale(0) rotate(-45deg);
      position: absolute;
      top: .3rem;
      left: .3rem;
      transition: .15s;

    }

  }

  &__text {
    font-family: inherit;
    color: inherit;
  }

}