.receive-newsletter {
  border-top: 1px solid @c-black-light;

  &__inner {
    padding-top: 3rem;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__col {
    width: 40%;
    display: flex;

    & .input {
      width: calc(100% - 1rem);
      flex-basis: 43rem;
      margin-right: 1rem;
    }

    &:first-child {
      flex-grow: 1;
      width: calc(60% - 3.5rem);
      margin-right: 3.5rem;
      align-items: start;
    }

    &.text {
      font-size: 2rem;
    }

  }



  &__btn {
    color: @c-black;
    background-color: @c-light-green;
    transition: @time;
    border: 1px solid transparent;
    border-radius: 0.3rem;
    padding: 1rem;

    flex-shrink: 0;

    &:hover {
      background-color: @c-light-purple;
      border: 1px solid @c-black;
    }
  }

}