.header {
  margin-bottom: 1.5rem;

  &__top {
    background-color: #272727;
    margin-bottom: 2rem;

  }

  &__top-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.7rem 0;
  }

  &__top-list {
    .resetList();
    display: flex;
  }

  &__top-item {
    margin-right: 3rem;

    &._active {
      .header__top-link {
        color: @c-red;
      }
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__top-link {
    color: @c-white;
    text-decoration: none;
    font-size: 1.4rem;
    line-height: 1.2;

    &:hover {
      color: @c-light-green;
    }
  }

  &__top-cabinet {
    margin-left: auto;
  }

  &__top-botton {
    .resetBtn();
    color: @c-white;
    font-size: 1.4rem;
    line-height: 1.2;
    padding-left: 1rem;
    padding-right: 1rem;
    border-right: 1px solid #FFFFFF;

    transition: @time;

    &:hover {
      color: @c-light-green;
    }

    &:last-child {
      border-right: none;
      padding-right: 0;
    }
  }

  &__top-languages {
    margin-left: 3rem;
  }

  &__content-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 3rem;

  }

  &__logo {
    display: inline-flex;
    flex-shrink: 0;
    position: relative;
    padding-right: 1rem;
    align-self: center;

    img {
      width: 15rem;
    }

  }

  &__logo-inner {
    display: flex;
  }

  &__search {
    flex-basis: 44.5rem;
    width: 25rem;
    margin: 0 1rem;
  }

  &__search-inner {
    display: flex;
    border: 1px solid @c-black-light;
    border-radius: 0.3rem;
  }

  &__search-input {
    width: 100%;
    padding: 1.3rem 1.5rem;
    border: none;
    overflow: hidden;
  }

  &__search-button {
    .resetBtn();
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    svg {
      width: 2rem;
      height: 2rem;
      fill: @c-black-light;
    }
  }

  &__phones {
    flex-shrink: 0;
    margin: 0 1rem;
  }

  &__basket {
    flex-shrink: 0;

    &-link {
      display: flex;
      align-items: center;
      cursor: pointer;

      text-decoration: none;
      color: inherit;

    }

    svg {
      margin-right: 0.5rem;
      width: 2rem;
      height: 2rem;
      fill: @c-black;
    }
  }

  &__bottom {
    border-bottom: 1px solid @c-black-light;
    padding-bottom: 1.5rem;
  }

  .header__content-burger {
    display: none;
  }

}