.catalog {

  &__title {
    font-weight: 600;
    font-size: 2.4rem;
    line-height: 1.2;
    color: @c-black;
    padding-bottom: 1rem;
    margin-bottom: 3rem;
    border-bottom: 1px solid @c-black-light;
    display: inline-flex;
  }

  &__list {
    .resetList();
    border-bottom: 1px solid @c-black-light;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 0.5rem;
    margin-bottom: 1.5rem;

    flex-wrap: wrap;



  }

  &__link {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: @c-gray-light;
    text-decoration: none;
    cursor: pointer;
    user-select: none;

    font-size: 1.8rem;
    font-weight: 600;

    transition: @time;

    &:hover {
      color: @c-black;
    }

    svg {
      width: 1.5rem;
      height: 1.5rem;
      fill: @c-black-light;
      transform: rotate(-90deg);

      transition: @time;
    }

    &._active {
      color: @c-light-green;
      svg {
        fill: @c-light-green;
        transform: rotate(0deg);
      }

    }

  }

  &__sub-menu {
    max-height: 0;
    //margin-top: 1rem;
    overflow: hidden;
    transition: max-height @time ease-out;
  }

  &__sub-list {
    .resetList();
    padding-left: 1rem;
  }

  &__sub-item {
    margin: 1rem 0;
  }

  &__sub-link {
    color: @c-black;
    cursor: pointer;
    text-decoration: none;

    transition: @time;

    &:hover {
      text-decoration: underline;
      color: @c-red;
    }
  }

}