.main-menu {

  &__list {
    .resetList();
    display: flex;
    justify-content: space-between;
    margin: 0 -0.5rem;
    position: relative;
  }

  &__item {
    margin: 0 0.5rem;

    &:hover {
      .main-menu__submenu-wrap {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__item-link {
    text-decoration: none;
    transition: @time;
    font-size: 2rem;
    line-height: 1.2;
    color: @c-black;
    padding-bottom: 1.5rem  ;

    &:hover {
      color: @c-light-green;

      .main-menu__submenu-wrap {
        opacity: 1;
        visibility: visible;

      }
    }
  }

  &__submenu-wrap {
    position: absolute;
    min-width: 100%;
    z-index: 11;
    top: calc(100% + 1.6rem);
    left: 0;
    right: 0;
    padding: 4rem 2rem;
    background: #F5F5F5;
    box-shadow: -5px 0 20px rgba(0, 0, 0, 0.25), 5px 5px 20px rgba(0, 0, 0, 0.25);
    opacity: 0;
    visibility: hidden;
    transition: @time;

  }

  &__submenu-inner {
    display: flex;

  }

  &__submenu-col {
    margin-right: 3rem;
    &:last-child {
      margin-right: 0;
    }
  }

  &__submenu {
    .resetList();
    z-index: 13;

    li {
      margin-bottom: 2rem;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__submenu-link {
    font-weight: 600;
    font-size: 2rem;
    line-height: 1.2rem;

    color: @c-black;
    transition: @time;
    text-decoration: none;

    &:hover {
      color: @c-light-green;
    }
  }

  &__sub-submenu-list {
    .resetList();
    margin-top: 1.5rem;

    li {
      margin-bottom: 1.5rem;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__sub-submenu-link {
    font-size: 1.8rem;
    line-height: 1.2;
    color: inherit;
    text-decoration: none;
    transition: @time;

    &:hover {
      color: @c-light-green;
    }
  }

}