.rating {
  display: inline-flex;

  &__item {
    flex-shrink: 0;
    width: 3rem;
    height: 3rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;

    &._fill svg {

      &._fill {
        opacity: 1;
      }
    }
  }

  svg {
    fill: @c-light-green;
    width: 2.5rem;
    height: 2.5rem;
    transition: opacity @time;
    opacity: 0;
    position: absolute;

    &._stroke {
      opacity: 1;
      fill: @c-gray;
    }
  }

}