.my-basket {
  @media (max-width: @md) {
    &__item-box {
      display: block;
    }

    &__item-img {
      margin: 0 auto 1rem;

    }

    &__item-description {
      width: 100%;
    }

    &__item-kod {
      margin-bottom: 1rem;
    }

    &__item-remove {
      margin-bottom: 1rem;
    }

    &__item-quantity {
      margin: 0 0 1rem;
    }

    &__item-price {
      margin: 0 0 1rem;
    }

    &__total-text {
      font-size: 1.8rem;
    }

    &__total-number {
      font-size: 2rem;
    }

    &__buttons-inner {
      flex-direction: column;
    }

    &__btn {
      width: 100%;

     &:first-child {
      margin: 0 0 1rem;
     }
    }


  }
}