.footer {
  background-color: @c-black;
  padding-top: 5.5rem;

  &__top-items {
    margin-bottom: 5rem;
    display: flex;
    justify-content: space-between;
  }

  &__top-col {

    &._menu {
      flex-basis: 47.5rem;
    }

    &._info {
      flex-basis: 38rem;
    }

  }

  &__bottom {
    border-top: 1px solid @c-white;
    padding: 0.8rem 0;
    color: @c-white;
    text-align: center;

    font-size: 1.2rem;
    line-height: 1.25;
    letter-spacing: 0.05em;
  }

  &__menu-wrap {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__menu {
    .resetList();
    margin-right: 5rem;

    &:last-child {
      margin-right: 0;
    }

    li {
      margin-bottom: 2rem;
    }

    a {
      font-size: 1.8rem;
      line-height: 1.22;
      letter-spacing: 0.05em;
      text-decoration: none;
      color: @c-white;

      transition: @time;

      &:hover {
        color: @c-light-green;
      }
    }
  }

  &__info {
    .resetList();
    margin: 0 2rem;

    li {
      font-size: 1.8rem;
      line-height: 1.22;
      letter-spacing: 0.05em;
      text-decoration: none;
      color: @c-white;

      margin-bottom: 3rem;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__schedule {
    display: flex;
    flex-direction: column;
  }

  &__social-networks {
    .resetList();

    li {
      margin-bottom: 3rem;
    }
  }

  &__bottom-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    font-size: 1.2rem;

  }

  &__dev-descr {
    font-size: 1.4rem;

    color: @c-black;
    margin-right: 1rem;
  }

  &__dev {
    margin: 1rem;

    display: flex;
    align-items: center;
    background-color: #fff;
    padding: 1rem;
    border-radius: 1rem;

    img {
      width: 135px;
      height: 13px;
    }
  }

}