.my-basket {
  margin: 4rem 0 6rem;

  &__title {
    font-weight: 500;
    font-size: 4rem;
    letter-spacing: 0.1em;
    color: @c-black;

    text-align: center;
    margin-bottom: 4rem;
  }

  &__item-quantity {
    margin: 0 1rem;
  }

  &__item {
    border-bottom: 1px solid @c-black-light;
    padding-bottom: 3rem;
    margin-bottom: 3rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

&__item-box {
  display: flex;
  justify-content: space-between;
}

  &__item-img {
   width: 25.5rem;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    position: relative;

    margin-right: 3rem;

    &::before {
      content: '';
      display: block;
      width: 100%;
      padding-top: 100%;
    }

    img {
      width: 100%;
      height: 100%;
      position: absolute;
      object-fit: contain;
    }
  }

  &__item-description {
    flex-basis: 25.5rem;

    display: flex;
    flex-direction: column;
    align-items: start;
  }

  &__item-name {
    font-weight: 600;
    font-size: 2rem;
    margin-bottom: 0.5rem;
  }

  &__item-kod {
    font-size: 1.4rem;
    margin-bottom: 3rem;
  }

  &__item-remove {
    .resetBtn();
    text-decoration: underline;
    font-size: 1.6rem;
  }

  &__item-price {
    font-weight: 600;
    font-size: 2rem;
    margin: 0 1rem;
    flex-shrink: 0;
  }

  &__item-total {
    font-weight: 600;
    font-size: 2.4rem;
    flex-shrink: 0;
  }

  &__total {
    padding: 2.2rem 0;
    background: @c-light-purple;
    border: 1px solid @c-black-light;
    margin-bottom: 4rem;
  }

  &__total-inner {
    display: flex;
    align-items: center;
  }

  &__total-text {
    margin-left: auto;
    margin-right: 1.5rem;

    font-weight: 600;
    font-size: 2.4rem;
  }

  &__total-number {
    font-weight: 600;
    font-size: 3rem;
  }

  &__buttons-inner {
    display: flex;
    align-items: center;
  }

  &__btn {
    &:first-child {
      margin-left: auto;
      margin-right: 2.5rem;
      border: 1px solid @c-black-light;
      background-color: transparent;
      color: @c-black;
      transition: @time;

      &:hover {
        background-color: @c-black;
        color: @c-white;
      }
    }
  }

}