.popular {
margin-bottom: 6rem;

  &__wrap {
    position: relative;
  }

  &__list {
    display: flex;
  }

  &__wrap-slider {
    overflow: hidden;
    margin:0 5rem;
  }

  &__item {

    width: calc((100% - 1px * 3) / 4);

    margin-right: 1rem;

    &:last-child {
      margin-right: 0;
    }

    flex-shrink: 0;

    & .card__item {
      margin: 0;
      width: 100%;
      height: 100%;
    }
  }

  &__carousel-navs {
    display: flex;
    justify-content: space-between;

    & .pagination {
      display: none;
    }

  }

  &__carousel-nav {
    position: absolute;
    top: 50%;
    height: 4rem;

    &._prev {
      left: 0;
      transform: translateY(-50%) rotate(90deg);
    }

    &._next {
      right: 0;
      transform: translateY(-50%) rotate(-90deg);
    }
  }

}