.bread-crumbs {

  margin: 1.5rem 0 3rem;

  &__list {
    .resetList();
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    display: flex;
    align-items: center;
    padding: 0 0.5rem;
    position: relative;

    &:before {
      content: '';
      width: 0.2rem;
      height: 0.2rem;
      background-color: @c-gray;
      position: absolute;
      right: 0;

    }

    &:last-child {
      &:before {
        content: none;
      }
    }

    a {
      text-decoration: none;
      color: @c-gray;

      &:hover {
        color: @c-gray-light;
      }
    }
  }

}