.together-cheaper {
  margin-bottom: 6rem;

  & .card__title {
    text-align: center;
    margin-bottom: 4rem;
  }

  &__wrap {
    .pagination {
      display: none;
    }
  }

  &__list {
    display: flex;

  }

  &__item {
    width: calc((100% - 1px * 4) / 5);

    margin-right: 1rem;
    flex-shrink: 0;

    opacity: 0.5;

    &.swiper-slide-visible {
      opacity: 1;
    }

    &:last-child {
      margin-right: 0;
    }

    & .card__item {
      margin: 0;
      width: 100%;
      height: 100%;
    }

  }

}