.main-carousel {
  margin-bottom: 5rem;

  &__wrap {
    background: url("../img/jpg/main-bg.jpg") no-repeat;
    overflow: hidden;
    margin-bottom: 1.5rem;
    position: relative;
  }

  &__list {
    display: flex;
  }

  &__item {
    width: 100%;
    flex-shrink: 0;

  }

  &__btn-close {
    .resetBtn();
    position: absolute;
    right: 1rem;
    top: 1rem;
    padding: 1rem;
    border-radius: 50%;
    background-color: @c-white;
    z-index: 10;

    svg {
      width: 1.5rem;
      height: 1.5rem;
      fill: @c-gray-light;
      transition: @time;

      &:hover {
        fill: @c-red;
        transform: scale(1.2);
      }
    }

  }

  &__item-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5rem 3.5rem;
  }

  &__item-right {
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  &__item-left {
    text-align: center;
  }

  &__item-title {
    margin: 0 0 2rem 0;
  }

  &__item-text {
    p {
      margin: 0 0 3rem 0;
    }
  }

}