.carousel-nav {
  .resetBtn();
  align-items: center;
  justify-content: center;
  width: 3.6rem;
  height: 2rem;
  color: @c-black;
  transition: color @time, opacity @time;

  svg {
    width: 100%;
    height: 100%;
    fill: @c-black;
    transition:  @time;
  }

  &._prev {
    transform: rotate(90deg);
  }

  &._next {
    transform: rotate(-90deg);
  }

  &:not(._disabled):hover {
    svg {
      fill: @c-light-green;
      transform: scale(1.2);
    }
  }

  &._lock {
    display: none;
  }

  &._disabled {
    cursor: auto;
    opacity: .3;
  }

}

.pagination {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  .swiper-pagination-bullet {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: 1px solid @c-black-light;
    flex-shrink: 0;
    margin: .5rem;
    cursor: pointer;
    background-color: transparent;
    transition: background-color @time;
  }

  .swiper-pagination-bullet-active {
    background-color: @c-black-light;
    cursor: auto;
  }
}