.ware {
  @media (max-width: @md) {
    &__inner {
      display: block;
    }

    &__status {
      flex-wrap: wrap;
    }

    &__left {
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 2rem;
    }

    &__slider-item {
      width: 10rem;
    }

    &__right {
      max-width: 100%;
    }

    &__head {
      margin-bottom: 1rem;
    }

    &__quantity {
      margin-bottom: 1rem;
    }
  }

  @media (max-width: @sm) {
    &__head {
      display: block;
    }

    &__head-col {
      width: 100%;
    }

    &__advertising {
      .card__item {
        flex-direction: column;
      }
    }

    &__buttons {
      flex-direction: column;
    }

    &__btn {
      margin-bottom: 1rem;

      &:first-child {
        margin-right: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

  }

}