.footer {
  @media (max-width: @md) {
    &__top-items {
      display: block;
    }
    &__top-col {

      &._menu {
        margin-bottom: 1.5rem;
      }

      &._info {
        flex-basis: 100%;
        margin-bottom: 1.5rem;
      }
    }

    &__menu-wrap {
      justify-content: start;
    }

    &__menu {
      margin: 0 2rem;
      &:last-child {
        margin: 0 2rem;
      }
    }

    &__social-networks {
      display: flex;

      li {
        margin: 0 1rem;
      }
    }

  }
}