.menu-mobile {
  position: fixed;
  z-index: 98;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  background-color: rgba(0, 0, 0, .5);
  visibility: hidden;
  opacity: 0;
  transition: @time;
  overflow-y: auto;

  &._active {
    visibility: visible;
    opacity: 1;
  }

  &__side {
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 99;
    width: 32rem;
    min-height: 100%;
    background: @c-white;
    padding: 4rem 1.5rem;

    transition: @time;

    transform: translateX(-100%);
  }

  &._active &__side {
    transform: translateX(0);
  }

  &__list {
    .resetList();
    border-bottom: 1px solid @c-black-light;
    margin-bottom: 1.5rem;
  }

  &__item {
    margin: 0 0 1rem 0;
  }

  &__link {
    font-size: 1.8rem;
    color: inherit;
    text-decoration: none;

    transition: @time;

    &:hover {
      color: @c-light-green;
    }
  }

  &__list-buttons {
    .resetList();
    border-bottom: 1px solid @c-black-light;
    margin-bottom: 1rem;
  }

  &__list-button {
    margin: 0 0 1rem 0;
  }

  &__lang {
    .resetList();
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__lang-item {
    padding-left: 1.2rem;
    padding-right: 1.2rem;
    border-radius: 5rem;
    border: 1px solid transparent;
    font-size: 14px;
    line-height: 26px;
    text-transform: uppercase;

    &._active {
      border: 1px solid @c-black-light;
    }

    a {
      text-decoration: none;
      color: inherit;
    }

  }

  &__close {
    .resetBtn();
    position: absolute;
    right: 1.5rem;
    top: 1.5rem;

    svg {
      width: 2rem;
      height: 2rem;
      fill: @c-black-light;
      transition: @time;

      &:hover {
        fill: @c-red;
      }
    }
  }

}